.App {
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  color: white;
}

.App__Aside {
  width: 50%;
  background-color: #ffffff;
  padding: 14% 10%;
  overflow: auto;
}

.AsideHeading {
  color: #3e3e58;
  font-size: 4vw;
  font-weight: 200;
  font-family: "Gruppo", sans-serif;
}

.AsideHeadingBold {
  color: #3e3e58;
  font-weight: 900;
  margin-bottom: 20px;
  font-family: "Gruppo", sans-serif;
}

.App__Form {
  width: 50%;
  background-color: #3e3e58;
  padding: 14% 10%;
  overflow: auto;
}

.PageSwitcher {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  margin-bottom: 10%;
}

