.FormCenter {
  margin-bottom: 100px;
}

.FormTitle {
  color: #707c8b;
  font-weight: 300;
  font-size: 0.8em;
  margin-bottom: 50px;
  font-family: "Gruppo";
}

.FormHeading {
  color: #ffffff;
  font-size: 4vw;
  font-weight: 200;
  font-family: "Gruppo", sans-serif;
}

.FormHeadingBold {
  color: #ffffff;
  font-weight: 900;
  margin-bottom: 5%;
  font-family: "Gruppo", sans-serif;
}

.FormTitle__Link {
  color: #707c8b;
  text-decoration: none;
  display: inline-block;
  font-size: 1.4vw;
  margin: auto;
  padding-bottom: 5px;
  padding-left: 7%;
  padding-right: 7%;
}

.FormTitle__Link:first-child {
  margin-left: 0;
}

.FormTitle__Link--Active {
  color: white;
  border-bottom: 0.45vw solid #199087;
}

.FormField {
  margin-bottom: 10%;
}

.FormField__Label {
  display: block;
  text-transform: uppercase;
  font-size: 0.9em;
  color: white;
}
.FormField__Input {
  width: 85%;
  background-color: transparent;
  border: none;
  color: white;
  outline: none;
  border-bottom: 1px solid #445366;
  font-size: 0.9em;
  font-weight: 300;
  padding-bottom: 10px;
  margin-top: 10px;
}

.FormField__Input::-webkit-input-placeholder {
  color: #616e7f;
}

.FormField__Input::-ms-input-placeholder {
  color: #616e7f;
}

.FormField__Input::placeholder {
  color: #616e7f;
}

.FormField__Button {
  background-color: #52c4b9;
  color: white;
  border: none;
  outline: none;
  border-radius: 25px;
  padding: 15px 70px;
  font-size: 0.9em;
  font-weight: 500;
}

.FormField__Link {
  color: #66707d;
  text-decoration: none;
  display: inline-block;
  border-bottom: 1.5px solid #225e62;
  padding-bottom: 5px;
}

.FormField__CheckboxLabel {
  color: #646f7d;
  font-size: 0.9em;
}

.FormField__Checkbox {
  position: relative;
  top: 1.5px;
}

.FormField__TermsLink {
  color: white;
  border-bottom: 1px solid #199087;
  text-decoration: none;
  display: inline-block;
  padding-bottom: 2px;
  margin-left: 5px;
}



